import React from 'react';
import './index.css'
const html ='data:image/webp;base64,'
const css ='data:image/png;base64,'
const javascript ='data:image/png;base64,'
const react = 'data:image/png;base64,'
const nodejs = 'data:image/png;base64,'
const mongo = 'data:image/png;base64,'
const mysql = 'data:image/png;base64,'
const redis = 'data:image/png;base64,'
const git = 'data:image/png;base64,'
const python = 'data:image/jpeg;base64,'
const websocket = 'data:image/png;base64,'
const vscode ='data:image/jpeg;base64,'
const postman ='data:image/jpeg;base64,'
const androidstudio ='data:image/jpeg;base64,'
const docker ='data:image/png;base64,'
const pycharm ='data:image/jpeg;base64,'
const vmware ='data:image/png;base64,'

export default function Skills() {
    const skills = [
        {name: 'HTML', img:html},
        {name: 'CSS', img:css},
        {name: 'JavaScript', img:javascript},
        {name: 'React', img:react},
        {name: 'Node.js', img:nodejs},
        {name: 'MongoDB',img:mongo},
        { name: 'MySql', img: mysql },
        { name: 'Redis', img: redis },
        { name: 'Git', img: git },
        { name: 'Python', img: python },
        { name: 'WebSocket', img: websocket },
    ]
    const tools=[
      { name:'VSCode', img:vscode},
      { name: 'Postman', img: postman },
      { name: 'Android Studio', img: androidstudio },
      { name: 'Docker', img: docker },
      { name: 'Pycharm', img: pycharm },
      { name: 'Vmware', img: vmware },

    ]
  return (
    <div className="skills">
      <h2>Professional Skillset</h2>
        <div className="skill-grid">
            {skills.map((skill, index) => (
                <div key={index} className="skill">
                    <img src={skill.img} alt={skill.name} />
                </div>
                    ))}
        </div>     
    
      <h2>Tools I Use</h2>
        <div className="skill-grid">
            {tools.map((skill, index) => (
                <div key={index} className="skill">
                    <img src={skill.img} alt={skill.name} />
                </div>
                    ))}
        </div>     
    </div>
  )
}
